.news-page {
  overflow: hidden;
}

.news-page__top {
  padding-top: 1rem;
}

.news-page__head {
  margin-top: 5rem;
}
@media (max-width: 480px) {
  .news-page__head {
    margin-top: 2.5rem;
  }
}

.news-page__slider {
  position: relative;
  padding: 0 0 1.5rem 0;
  margin-top: 2.5rem;
}
@media (max-width: 480px) {
  .news-page__slider {
    margin-top: 1.5rem;
  }
}
.news-page__slider .news__slider-pagination {
  width: auto;
  bottom: 0;
  top: auto;
  left: 1rem;
  right: 1rem;
  z-index: 1;
  border-radius: 2.5rem;
  overflow: hidden;
  display: none;
}
@media (max-width: 480px) {
  .news-page__slider .news__slider-pagination {
    display: block;
  }
}
.news-page__slider .news__slider-pagination .swiper-pagination-progressbar-fill {
  background-color: var(--color-red);
  border-radius: 2.5rem;
}
.news-page__slider .news__card {
  background-color: var(--light-color);
  padding: 1rem;
  border-radius: 1rem;
}
.news-page__slider .news__card-link {
  padding: 0.5rem 1rem;
}

.news-page__slide {
  max-width: 28rem;
}

.news-page__filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 5rem;
}
@media (max-width: 480px) {
  .news-page__filters {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 1.5rem;
  }
}

@media (max-width: 480px) {
  .news-page__selects {
    width: 100%;
  }
}

.news-page__select {
  background-color: transparent;
  border: none;
  font-family: var(--font-pt);
  font-weight: 700;
  padding: 0.5rem;
  cursor: pointer;
  position: relative;
  z-index: 1;
  background-repeat: no-repeat;
  background-position: center right 1rem;
}
@media (max-width: 480px) {
  .news-page__select {
    width: 100%;
    background-color: var(--light-color);
    padding: 1rem;
    border-radius: 0.5rem;
    color: var(--silver-color);
    text-align: left;
  }
}
@media (max-width: 480px) {
  .news-page__select + .news-page__select {
    margin-top: 0.5rem;
  }
}
.news-page__select_date {
  background-image: url("../img/icons/calendar.svg");
}
.news-page__select_city {
  position: relative;
}
.news-page__select_city::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 1rem;
  transform: translateY(-50%) rotate(-90deg);
  width: 1.5rem;
  height: 1.5rem;
  background-image: url("../img/icons/arrow-up.svg");
}

.news-page__reset {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--color-red);
  font-family: var(--font-pt);
  font-weight: 700;
}
@media (max-width: 480px) {
  .news-page__reset {
    margin-left: auto;
    margin-top: 1rem;
  }
}

.news-page__articles {
  margin: 2.5rem 0;
}
@media (max-width: 768px) {
  .news-page__articles_desctop {
    display: none;
  }
}
.news-page__articles_mobile {
  display: none;
}
@media (max-width: 768px) {
  .news-page__articles_mobile {
    display: block;
  }
}
.news-page__articles .news__article {
  padding: 1.5rem 0 2.5rem 0;
  border-top: 1px solid rgba(180, 180, 180, 0.2);
}
.news-page__articles .news__article:last-child {
  border-bottom: 1px solid rgba(180, 180, 180, 0.2);
}
.news-page__articles .news__card {
  background-color: var(--light-color);
  padding: 1rem;
  border-radius: 1rem;
}
.news-page__articles .news__card + .news__card {
  margin-top: 0.5rem;
}
.news-page__articles .news__card-link {
  padding: 0.5rem 1rem;
}

.news-page__more {
  width: 100%;
  text-align: center;
}